import React, { useEffect, useState } from "react";
import { MixmotionPlayer } from "mixmotion-player";
import { faCircleLeft } from "@fortawesome/free-solid-svg-icons";

const customButtons = [
  /*{ action: "save", align: "right" },*/
  /*{ action: "previous", align: "center" },*/
  { action: "playpause", align: "center" , label: "Play/Pause"},
  { action: "collapse", align: "right" },
  /*{ action: "next", align: "center" },*/
  {
    action: "custom",
    align: "left",
    label: "Back",
    faIcon: faCircleLeft,
    onPress: () => {
     window.history.go(-1);
    },
  }
];

function Vplayer() {
  const [url, setUrl] = useState("");

  useEffect(() => {
    try {
      const getVideoUrlFromQuery = () => {
        const params = new URLSearchParams(window.location.hash.slice(1));
        const video = params.get('video');

        switch (video) {
          case 'video1':
            return "https://www.mixcloud.com/discover/trance/?order=latest";
          case 'video2':
            return "https://www.mixcloud.com/discover/rock/?order=latest";
          default:
            return "https://www.mixcloud.com/discover/trance/?order=latest";
        }
      };

      const videoUrl = getVideoUrlFromQuery();
      console.log("Video URL:", videoUrl); // Debug output
      setUrl(videoUrl);
    } catch (error) {
      console.error("Error in useEffect:", error);
    }
  }, []);

  // Check if url is empty or has a valid value
  if (!url) {
    return <div>Loading...</div>;
  }

  return <MixmotionPlayer url={url} />;
}

export default Vplayer;